.cards-welcome {
  a {
    text-decoration: none;
    .cards1 {
      background-image: url("../images/index/bg-cards1.png");
      padding: 1rem 3rem;
      text-align: center;
      @media (max-width: 575.98px) {
        margin-bottom: 1rem;
      }
    }
    .cards2 {
      background-image: url("../images/index/bg-cards3.png");
      padding: 1rem 3rem;
      text-align: center;
      transform: translateY(15px);
      @media (max-width: 575.98px) {
        transform: translateY(0);
        margin-bottom: 1rem;
      }
    }
    .cards3 {
      background-image: url("../images/index/bg-cards2.png");
      padding: 1rem 3rem;
      text-align: center;
      @media (max-width: 575.98px) {
        margin-bottom: 1rem;
      }
    }
    .cards1,
    .cards2,
    .cards3 {
      transition: transform 0.3s;
    }
    &:hover {
      .cards1,
      .cards2,
      .cards3 {
        transform: scale(1.05);
      }
    }
  }
  .img-cards {
    width: 40%;
  }
  .title-cards {
    font-family: "radicalis";
    font-weight: 400;
    color: $white;
    font-size: 1.5rem;
  }
  .text-cards {
    font-family: "Roboto";
    font-weight: 600;
    color: $white;
    font-size: .8rem;
    line-height: 15px;
  }
}
