.seccion-team {
  background-color: $gray-claro;
  padding-top: 3rem;
  padding-bottom: 3rem;
  h5 {
    color: $blue-siroco;
    font-weight: 400;
    span {
      font-weight: 800;
    }
  }
  a {
    color: $blue-siroco;
    text-decoration: none;
    font-size: 1.2rem;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-claro;
    }
  }
}