.section-timeline {
  background-image: url("../images/olas.png");
  background-color: $white;
  background-position: top;
  background-size: contain;
  background-repeat: no-repeat;
  padding-top: 8%;
  .timeline {
    position:relative;
    margin: 50px auto 0;
    width: 1000px;
    padding: 50px 0;
    &:before {
      content: '';
      position:absolute;
      height:100%;
      left:50%;
      width:2px;
      top:-20px;
      background: lightgray;
      z-index:1;
      @media (max-width: 767.98px){
        left:20px;
      }
    }
    p {
      color: $gray-siroco;
      font-family: 300;
    }
    ul{
      margin:0;
      padding:0 ;
      li{
        list-style:none;
        box-sizing:border-box; 
        line-height:normal;
        position:relative;
        width:50%;
        padding: 40px 70px;
        @media (max-width: 767.98px) {
          padding: 0 70px 40px 70px;
        }
        &:nth-child(1){
          float:left;
          text-align:right;
          clear:both;
          @media (max-width: 767.98px) {
            width:100%;
            text-align:left;
            padding-left:50px;
          }
          .left_content{
            position:absolute;
            top: -10px;
            right: -400px;
            max-width: 350px;
            margin:0;
            padding:8px 16px;
            color:#fff;
            border-radius:18px;
            transition: all .5s;
            img {
              border-left: 8px solid #48d7fd;
              border-radius: 25px;
              display: block;
              @media (max-width: 767.98px){
                display: none;
              }
            }
            &:hover {
              transform: scale(1.2);
            }
            @media (max-width: 767.98px){
              top: 130px;
              left: 35px;
              right: inherit;
              max-width: 250px;
            }
          }
        }
        &:nth-child(2){
          float:right;
          text-align:left;
          clear:both;
          @media (max-width: 767.98px) {
            width:100%;
            text-align:left;
            padding-left:50px;
          }
          .left_content{
            position:absolute;
            top: -10px;
            left: -400px;
            max-width: 350px;
            margin:0;
            padding:8px 16px;
            color:#fff;
            border-radius:18px;
            transition: all .5s;
            img {
              border-right: 8px solid $yellow;
              border-radius: 25px;
              display: block;
              @media (max-width: 767.98px){
                display: none;
              }
            }
            &:hover {
              transform: scale(1.2);
            }
            @media (max-width: 767.98px){
              top: 130px;
              left: 35px;
              right:inherit;
              max-width: 250px;
            }
          }
        }
        &:nth-child(3){
          float:left;
          text-align:right;
          clear:both;
          @media (max-width: 767.98px) {
            width:100%;
            text-align:left;
            padding-left:50px;
          }
          .left_content{
            position:absolute;
            top: -10px;
            right: -400px;
            max-width: 350px;
            margin:0;
            padding:8px 16px;
            color:#fff;
            border-radius:18px;
            transition: all .5s;
            img {
              border-left: 8px solid #7164ea;
              border-radius: 25px;
              display: block;
              @media (max-width: 767.98px){
                display: none;
              }
            }
            &:hover {
              transform: scale(1.2);
            }
            @media (max-width: 767.98px){
              top: 130px;
              left: 35px;
              right:inherit;
              max-width: 250px;
            }
          }
        }
        &:nth-child(4){
          float:right;
          text-align:left;
          clear:both;
          @media (max-width: 767.98px) {
            width:100%;
            text-align:left;
            padding-left:50px;
          }
          .left_content{
            position:absolute;
            top: -10px;
            left: -400px;
            max-width: 350px;
            margin:0;
            padding:8px 16px;
            color:#fff;
            border-radius:18px;
            transition: all .5s;
            img {
              border-right: 8px solid #42cd83;
              border-radius: 25px;
              display: block;
              @media (max-width: 767.98px){
                display: none;
              }
            }
            &:hover {
              transform: scale(1.2);
            }
            @media (max-width: 767.98px){
              top: 130px;
              left: 35px;
              right:inherit;
              max-width: 250px;
            }
          }
        }
        &:nth-child(1):before{
          content: '';
          position:absolute;
          width:25px;
          height:25px;
          background:#48d7fd;
          border-radius:50%;
          right:-14px;
          top:65px;
          z-index:1;
          @media (max-width: 767.98px) {
            top:25px;
            left:9px;
          }
        }
        &:nth-child(2):before{
          content: '';
          position:absolute;
          width:25px;
          height:25px;
          background:$yellow;
          border-radius:50%;
          left:-11px;
          top:65px;
          z-index:1;
          @media (max-width: 767.98px) {
            top:25px;
            left:9px;
          }
        }
        &:nth-child(3):before{
          content: '';
          position:absolute;
          width:25px;
          height:25px;
          background:#7164ea;
          border-radius:50%;
          right:-14px;
          top:65px;
          z-index:1;
          @media (max-width: 767.98px) {
            top:25px;
            left:9px;
          }
        }
        &:nth-child(4):before{
          content: '';
          position:absolute;
          width:25px;
          height:25px;
          background:#42cd83;
          border-radius:50%;
          left:-11px;
          top:65px;
          z-index:1;
          @media (max-width: 767.98px) {
            top:25px;
            left:9px;
          }
        }
        .right_content {
          .title-timeline {
            padding:0px 2px 18px 0px;
            font-family: "Montserrat";
            font-weight: 800;
          }
          .title-timeline1 {
            color: #48d7fd;
          }
          .title-timeline2 {
            color: $yellow;
          }
          .title-timeline3 {
            color: #7164ea;
          }
          .title-timeline4 {
            color: #42cd83;
          }
        } 
      }
    }
    .left_content{
      padding-bottom:20px;
    }
    @media (max-width: 1000px){
      width:100%;
    }
    @media (max-width: 767.98px) {
      width:100%;
    }
  } 
}
