.seccion-slider-alumnos {
  background-color: $white;
  padding-top: 3rem;
  p.title {
    text-align: center;
    color: $blue-siroco;
    font-family: Montserrat;
    margin: 0;
  }
  h3.title {
    text-align: center;
    color: $blue-siroco;
    font-family: Montserrat;
    font-weight: bold;
  }
  .content-slider-alumnos-img {
    width: 60%;
    margin: 0 auto;
    text-align: center;
    @media (min-width: 575px) {
      width: 40%;
    }
    @media (min-width: 992px) {
      width: 30%;
    }
    .alumnos-img {
      margin-left: -40px;
      margin-right: -40px;
      .slick-list {
        .slick-track {
          padding-top: 30px;
          padding-bottom: 30px;
          .slick-slide {
            position: relative;
            margin: 0 -20px;
            opacity: 0;
            transition: all 0.3s;
            transition-delay: 0.3s;
            z-index: -1;
            .slide:after {
              content: "";
              position: absolute;
              right: 0;
              bottom: 0;
              width: 40%;
              height: 40%;
              opacity: 0;
              background-image: url('../images/index/comillas.png');
              background-size: contain;
              background-position-y: bottom;
              background-repeat: no-repeat;
              transition: all 0.3s;
              transition-delay: 0.3s;
            }
            &.slick-active {
              z-index: 1;
              opacity: 1;
              cursor: pointer;
            }
            &.slick-center {
              z-index: 2;
              transform: scale(1.2);
              .slide:after {
                opacity: 1;
              }
            }
            img {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .content-slider-alumnos-text {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    @media (min-width: 768px) {
      width: 65%;
    }
    @media (min-width: 992px) {
      width: 50%;
    }
    .alumnos-text {
      .slick-slide {
        padding: 0 2rem;
        p {
          font-family: Montserrat;
          color: $blue-siroco;
        }
        h3 {
          color: $blue-siroco;
          margin: 0;
          font-family: Montserrat;
          font-weight: bold;
        }
        .text {
          font-family: Roboto;
          color: $gray-siroco;
        }
      }
      .slick-prev{
        left: -10%;
        @media (min-width: 768px) {
          left: -20%;
        }
      }
      .slick-next {
        right: -10%;
        @media (min-width: 768px) {
          right: -20%;
        }
      }
      .slick-prev,
      .slick-next {
        width: 40px;
        height: 40px;
      }
      .slick-prev:before,
      .slick-next:before {
        display: none;
      }
    }
  }
}