.seccion-video {
  background-color: $white;
  padding-top: 3rem;
  padding-bottom: 3rem;
  a {
    color: $blue-siroco;
    text-decoration: none;
    font-size: 1.2rem;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-claro;
    }
  }
  #btn-video-yt {
    transition: all 0.3s;
    .logo-video-youtube {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 20%;
      transform: translate(-50%, -50%);
      opacity: 0.5;
      transition: all 0.3s;
    }
    &:hover {
      transform: scale(1.05);
      .logo-video-youtube {
        opacity: 1;
      }
    }
  }
  #videoIndex {
    .modal-content {
      background-color: transparent;
      border: none;
      .modal-header {
        border: none;
        .btn-close {
          border-radius: 50px;
          background: #fff url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23F14F78'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
        }
      }
    }
  }
}