.seccion-banner-inf {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: $gray-claro;
  .icon-banner {
    text-align: end;
    margin-right: 1rem;
    a {
      color: $blue-siroco;
      text-decoration: none;
      &:hover {
        color: $blue-siroco2;
      }
    }
  }
  p,
  .text-icon {
    font-family: "Roboto";
    font-weight: 400;
    font-size: .7rem;
    a {
      color: $blue-siroco;
      text-decoration: none;
      &:hover {
        color: $blue-siroco2;
      }
    }
  }
  .img-icon-banner {
    max-width: 60px;
  }
  .text-icon {
    border-left: 1px solid lightgray;
    padding-left: .5rem;
    height: 80px;
    display: flex;
    align-items: center;
  }
}