.seccion-colaboradores {
  padding-top: 8%;
  padding-bottom: 1rem;
  background-image: url("../images/olas.png");
  background-color: $white;
  background-position: top;
  background-size: contain;
  background-repeat: no-repeat;
  .colaboradores {
    width: 95%;
    margin: 0 auto;
    .slide {
      padding: 1rem;
      a {
        text-decoration: none;
        &:hover {
          img {
            transform: scale(1.2);
          }
        }
        img {
          margin: 0 auto;
          transition: all 0.3s;
        }
      }
    }
  }
}